<template>
  <div class="Messages plr" id="Messages">
    <div class="message" v-for="(message, index) in messages" :key="index" :class="{ direct: message.direct, left: message.left, loading: message.loading, pop0: message.pop0, pop1: message.pop1, lost: message.lost }" :id="message._id">
        <div class="message-item">
          <div class="message-body" v-html="message.body"></div>
          <div class="message-loading"></div>
          <div class="message-time">
            {{ message.timehm }}
            <div v-if="message.name" class="message-username">{{ message.name }}</div>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
export default {
  name: 'Messages',
  data () {
    return {
      messages: []
    }
  },
  computed: {
    messagesCounter: function () {
      return this.$store.state.messages.c
    }
  },
  methods: {
    showMessages (pop = false) {
      let statemsgs = Object.values(this.$store.state.messages).reverse()
      statemsgs.pop()
      this.messages = statemsgs
      setTimeout(() => {
        statemsgs.map((item) => {
          item = this.$store.state.messages[item._id]
          if (item.pop0) {
            delete item.pop0
          }
          if (item.pop1) {
            delete item.pop1
          }
          if (!pop && item.loading && ((Date.now() - item.time) > 10000)) {
            item.lost = true
          }
        })
        if (pop) {
          let last = statemsgs[0]._id
          this.$store.state.messages[last].pop1 = true
          setTimeout(() => {
            delete this.$store.state.messages[last].pop1
            this.showMessages()
          }, 500)
          if (statemsgs[0].direct) {
            let msg = document.getElementById('Messages').getBoundingClientRect().top
            let min = document.getElementById('Bar').offsetHeight + 70
            let max = this.$store.state.gameDisplay.mainHeight - 60
            let delta = (msg < min) ? msg - min : ((msg > max) ? msg - max : 0)
            if (delta) {
              document.getElementById('mainview').scrollTop += delta
            }
          }
        }
        this.$store.commit('lsMessages')
      }, 100)
    }
  },
  watch: {
    messagesCounter () {
      this.showMessages(true)
    }
  },
  created () {
    this.showMessages()
  }
}
</script>

<style lang="sass">
  .Messages
    margin: 1em auto;
    max-width: 500px;

  .message
    text-align: right;
    padding-bottom: .5em;
    opacity: .6;
    transform-origin: 100% 100%;
    box-sizing: border-box;
    max-width: 100%;

  .message-item
    display: inline-block;
    border-radius: 3px;
    padding: 0 1em;
    position: relative;
    max-width: 100%;

  .message-body
    font-size: .8em;
    color: #333;
    word-wrap: break-word;

  .message-body i
    font-style: normal;
    font-size: 1.5em;
    line-height: .1em;
    opacity: .3;

  .message.direct
    padding-left: 2em;
    opacity: 1;

  .message.direct .message-item
    max-width: calc(100% - 2em);

  .message.direct .message-item
    background: #e0e0e0;
    padding: .5em 1em;

  .message.direct .message-time
    margin-top: .5em;
    color: #666;

  .message.direct .message-body
    color: #000;
    font-size: 1em;

  .message.left
    text-align: left;
    opacity: 1;
    transform-origin: 0% 100%;

  .message.direct.left
    padding-left: 0;
    padding-right: 2em;

  .message.direct.left .message-item
    background: $grey1;

  .message-time
    font-size: .7em;
    color: #bbb;

  .message-username
    display: inline;

  .message-loading
    display: none;
    position: absolute;
    right: 1em;

  .loading .message-loading
    display: block;

  .loading .message-time
    opacity: 0;

  .message-loading:after
    content: '‚Ä¢';
    display: inline-block;
    opacity: 0;
    animation: loading 1s infinite;

  @keyframes loading
    0%, 100%
      transform: scale(1);
      opacity: .5;
    50%
      transform: scale(.5);
      opacity: .1;

  .loading.lost .message-loading
    display: block;
    right: .75em;

  .loading.lost .message-time
    opacity: 0;

  .loading.lost .message-loading:after
    content: '‚õî';
    opacity: 1;
    animation: none;

  .message.pop0
    display: none;

  .message.pop1
    animation: message-pop .5s ease;

  @keyframes message-pop
    0%
      max-height: 0;
      transform: perspective(1px) scale(.1);
    100%
      max-height: 100vh;
      transform: perspective(1px) scale(1);

</style>



