<template>
  <div>
    <div class="form Game-form plr">
      <transition-group name="show">
        <div v-show="gameFight" class="Game-joined" key="joined1">
          <p>
            <b>{{ gameFight }}</b>
          </p>
        </div>
        <div v-if="!gameLinkCopied" class="Game-link" key="copy0">
          <p v-html="t('Send this link ...')"></p>
          <div class="form-row">
            <label for="gameLink">{{ t('Link') }}</label>
            <textarea type="text" v-model="gameLink" id="gameLink"></textarea>
          </div>
          <a class="button" v-clipboard:copy="gameLink" v-clipboard:success="handleCopy">{{ t('Copy link') }}</a>
        </div>
        <div v-else class="Game-link-copied" key="copy1">
          <p v-html="t('The link is copied.')"></p>
        </div>
      </transition-group>
      <div class="Game-new">
        <a class="button grey" @click="handleNew">{{ t('New game') }}</a>
      </div>
    </div>
  </div>
</template>

<script>
import { t } from 'localizify'
export default {
  name: 'Game',
  data () {
    return {
      t,
      gameLinkCopied: false,
      waitRedirect: false,
      gameFight: ''
    }
  },
  computed: {
    gameLink: function () {
      return this.$store.state.game.link()
    },
    version: function () {
      return this.$store.state.game.__v
    }
  },
  methods: {
    handleCopy (e) {
      this.gameLinkCopied = true
    },
    handleNew () {
      this.gameLinkCopied = false
      this.$store.dispatch('newGame')
    },
    watchVersion () {
      if (this.$store.state.redirect) {
        this.gameFight = t('üì° Connecting...')
        if (this.$store.state.game.playerId1) {
          this.gameFight = t('üîó Joined!')
          this.$store.commit('setRedirect', false)
          setTimeout(() => {
            this.$router.push('/')
          }, 3000)
        }
      } else {
        let joinGameId = this.$route.params.joinGameId
        if (joinGameId) {
          this.$store.commit('setRedirect', true)
          this.$store.dispatch('newGame', joinGameId)
        }
      }
    }
  },
  created () {
    this.watchVersion()
  },
  watch: {
    version () {
      this.watchVersion()
    }
  }
}
</script>
<style lang="sass">
  .Game-form
    padding-top: 1em;

  .Game-new
    padding-top: 1em;

</style>



