<template>
  <div>
    <div class="form User-form plr">
      <transition name="show" mode="out-in">
        <div v-if="(userPhase === 'new') || (userPhase === 'emailVerified')" key="new">
          <div class="form-row">
            <label for="name">{{ t('Name') }}</label>
            <input type="text" v-model="name" id="name" disabled>
          </div>
          <div class="form-row">
            <a class="button grey" @click="playAsGuest">{{ t('Change random name') }}</a>
          </div>
          <p class="User-form-center">
            {{ t('or sign in:') }}
          </p>
          <texts></texts>
          <form class="form form-signin" @submit.prevent="formHandle('signin')">
            <div class="form-row">
              <label for="email">{{ t('Email') }}</label>
              <input type="email" v-model="email" id="email" name="email" :disabled="clas.signin==='loading'" v-validate="'required|email'" :data-vv-as="t('Email')">
              <transition name="show">
                <div class="form-error" v-if="errors.has('email')">{{ errors.first('email') }}</div>
              </transition>
            </div>
            <div class="form-row">
              <label for="password">{{ t('Password') }}</label>
              <input type="password" v-model="password" id="password" name="password" :disabled="clas.signin==='loading'" v-validate="'required'" :data-vv-as="t('Password')">
              <transition name="show">
                <div class="form-error" v-if="errors.has('password')">{{ errors.first('password') }}</div>
              </transition>
            </div>
            <div class="form-row buttons" :class="clas.signin">
              <button type="submit" :disabled="clas.signin==='loading'">{{ t('Sign in') }}</button>
            </div>
          </form>
          <p class="User-form-center" v-html="t('signedAdvantages')"></p>
        </div>
        <div v-if="userPhase === 'secret'" key="secret">
          <p>
            {{ t('The secret code was send on email') }} {{email}}.
          </p>
          <texts></texts>
          <form class="form form-secret" @submit.prevent="formHandle('secret')" :disabled="clas.secret==='loading'">
            <div class="form-row" :class="clas.secret">
              <label for="secret">{{ t('Secret code') }}</label>
              <input type="text" v-model="secret" id="secret" name="secret" :disabled="clas.secret==='loading'" v-validate="'required'" :data-vv-as="t('Secret code')">
              <transition name="show">
                <div class="form-error" v-if="errors.has('secret')">{{ errors.first('secret') }}</div>
              </transition>
            </div>
            <div class="form-row">
              <button type="submit">{{ t('Confirm') }}</button>
            </div>
          </form>
          <div class="form-row buttons">
            <a class="button grey" @click="phaseHandle('new')">{{ t('I didn`t get it') }}</a>
          </div>
        </div>
        <div v-if="userPhase === 'signed'" key="signed">
          <p>
            {{ t('Signed in.') }}
          </p>
          <texts></texts>
          <form class="form" @submit.prevent="formHandle('name')" :disabled="clas.name!=='change'" :class="clas.name">
            <div class="form-row" :class="clas.name">
              <label for="name">
                {{ t('Name') }}
                <transition name="show-del1">
                  <div class="label-check" v-if="clas.name==='busy'">{{ t('Busy') }}</div>
                </transition>
              </label>
              <input type="text" v-model="name" id="name" :disabled="clas.name==='loading'" @keyup="keyupHandle('name')">
            </div>
            <transition name="show-del1">
              <div class="form-row" v-if="clas.name==='change'">
                <button type="submit">{{ t('Change name') }}</button>
              </div>
            </transition>
          </form>
          <form class="form" @submit.prevent="formHandle('email')" :disabled="clas.email!=='change'" :class="clas.email">
            <div class="form-row" :class="clas.email">
              <label for="email">
                {{ t('Email') }}
                <transition name="show-del1">
                  <div class="label-check" v-if="clas.email==='busy'">{{ t('Busy') }}</div>
                </transition>
              </label>
              <input type="email" v-model="email" id="email" :disabled="clas.email==='loading'" @keyup="keyupHandle('email')">
            </div>
            <transition name="show-del1">
              <div class="form-row" v-if="clas.email==='change'">
                <button type="submit">{{ t('Change email') }}</button>
              </div>
            </transition>
          </form>
          <div class="form-row buttons">
            <a class="button grey" @click="phaseHandle('new')">{{ t('Sign Out') }}</a>
          </div>
        </div>
      </transition>
    </div>

  </div>
</template>

<script>
import Texts from '@/components/Texts'
import Vue from 'vue'
import VeeValidate, { Validator } from 'vee-validate'
import { t } from 'localizify'
import Vuels from 'vue-ls'

let vconfig = {}
Vue.use(Vuels, { namespace: 'vue__' })
let lang = Vue.ls.get('lang') || 'en'
if (lang !== 'en') {
  vconfig.locale = lang
  import(`vee-validate/dist/locale/${lang}`).then(locale => {
    Validator.localize(lang, locale)
  })
}
Vue.use(VeeValidate, vconfig)

export default {
  name: 'User',
  data () {
    return {
      t,
      name: this.$store.state.user.name,
      email: this.$store.state.user.email,
      password: '',
      secret: '',
      clas: {
        name: '',
        email: '',
        signin: '',
        secret: ''
      },
      userPhase: 'new'
    }
  },
  computed: {
    version: function () {
      return this.$store.state.user.__v
    },
    phase: function () {
      return this.$store.state.user.phase
    }
  },
  methods: {
    playAsGuest () {
      this.$store.dispatch('createUser')
      this.$store.dispatch('changePlayer')
      this.$router.push('/')
    },
    formHandle (field) {
      this.$validator.validateAll().then((result) => {
        if (result) {
          if (field === 'signin') { // signIn
            this.clas[field] = 'loading'
            this.$store.dispatch('signIn', {
              email: this.email,
              password: this.password
            })
              .then(() => {
                this.clas['signin'] = ''
              })
          } else if (field === 'secret') { // verifyEmail
            this.clas[field] = 'loading'
            this.$store.dispatch('verifyEmail', this.secret)
              .then(() => {
                this.clas['secret'] = ''
                this.secret = ''
              })
          } else { // change info on signed
            let val = this[field].trim()
            if ((val !== this.$store.state.user[field]) && (this.clas[field] === 'change')) {
              this.$store.dispatch('changePlayerInfo', {field, val})
                .then(() => {
                  this[field] = val
                  this.clas[field] = ''
                })
            }
          }
        }
      })
    },
    keyupHandle (field) {
      let val = this[field].trim()
      if (val && (val !== this.$store.state.user[field])) {
        this.clas[field] = 'check'
        this.$store.dispatch('checkValidInfo', {field, val})
          .then((response) => {
            this.clas[field] = 'change'
          })
          .catch((error) => {
            this.clas[field] = error.response.data
          })
      } else {
        this.clas[field] = ''
      }
    },
    phaseHandle (n) {
      this.userPhase = n
      this.$store.dispatch('setUserPhase', n)
    },
    watchVersion () {
      this.clas.name = ''
      this.clas.email = ''
      this.clas.signin = ''
      this.clas.secret = ''
      this.name = this.$store.state.user.name
      this.email = this.$store.state.user.email
      if (!this.email) this.password = ''
    },
    watchPhase () {
      this.userPhase = this.$store.state.user.phase
      if ((this.userPhase === 'emailVerified') && this.password) {
        this.formHandle('signin')
      }
    }
  },
  created () {
    this.watchPhase()
  },
  mounted () {
    this.$nextTick(function () {
      let userId = this.$route.params.userId
      let code = this.$route.params.code
      if (code && userId) {
        if (userId !== this.$store.state.user._id) {
          this.$store.state.user._id = userId
        }
        this.$store.dispatch('setUserPhase', 'secret')
        this.secret = code
        this.formHandle('secret')
      }
    })
  },
  watch: {
    version () {
      this.watchVersion()
    },
    phase () {
      this.watchPhase()
    }
  },
  components: {
    Texts
  }
}
</script>
<style lang="sass" scoped>
  .User-form
    padding-top: 1em;

  .loading
    position: relative;

  .loading input
    color: #ccc;
    background: #f0f0f0;

  .loading:after
    content: '';
    position: absolute;
    width: 2em;
    height: 2em;
    right: 0;
    bottom: 0;
    border-radius: 300%;
    background: #000;
    opacity: 0;
    animation: loading 1s infinite;

  @keyframes loading
    0%, 100%
      transform: scale(.3);
      opacity: .5;
    50%
      transform: scale(.1);
      opacity: .1;

  .form-secret
    margin-top: 1em;

  label
    position: relative;

  .check label:after
    content: '';
    position: absolute;
    width: 1.5em;
    height: 1.5em;
    right: 0;
    bottom: 0;
    border-radius: 300%;
    background: #000;
    opacity: 0;
    animation: loading 1s infinite;

  .label-check
    position: absolute;
    right: 0;
    bottom: .2em;
    display: inline-block;
    background: $red;
    color: $white;
    padding: .1em .5em;
    font-size: .8em;
    border-radius: 2px;

  .User-form-center
    margin-top: 2em;
    text-align: center;

</style>



