<template>
<div  class="Board" :class="'player' + player" :style="boardWidth">
  <div class="Board-row Board-row-a">
    <cell type="CellRow" class="TurnIcon">
    </cell><cell v-for="(item) in boardIndexes.col" :key="item+0" type="CellCol" :content="item">
    </cell><cell type="CellR">
    </cell>
  </div>
  <div  v-for="(row, rowi) in boardIndexes.row" class="Board-row" :key="rowi">
    <cell type="CellRow" :content="row">
    </cell><cell v-for="(item, coli) in boardIndexes.col" type="Cell" :key="item+row" :row="rowi" :col="coli" :player="player">
    </cell><cell type="CellR">
    </cell>
  </div>
</div>
</template>

<script>
import Cell from '@/components/Cell'
export default {
  name: 'Board',
  data () {
    return {
      boardIndexes: this.$store.state.game.boardIndexes
    }
  },
  computed: {
    boardWidth: function () {
      return 'max-width:' + this.$store.state.gameDisplay.boardWidth + 'px'
    }
  },
  props: ['player'],
  components: {Cell}
}
</script>



