import Vue from 'vue'
import Vuex from 'vuex'
import Vuels from 'vue-ls'
import VueClipboard from 'vue-clipboard2'
import Push from 'push.js'
import axios from 'axios'
import io from 'socket.io-client'
import User from './user'
import Game from './game'
import Message from './message'
import localizify, { t } from 'localizify'
import { polyfill } from 'es6-promise'

polyfill()

Vue.use(Vuex)
Vue.use(Vuels, { namespace: 'vue__' })
Vue.use(VueClipboard)

let user = Vue.ls.get('user')
let game = Vue.ls.get('game')
let lang = Vue.ls.get('lang') || 'en'
let messages = Vue.ls.get('messages') || {c: 0}

localizify
  .add('en', require('../messages/en.json'))
  .add('ru', require('../messages/ru.json'))
  .setLocale(lang)

io.queue = []

const store = new Vuex.Store({
  state: {
    io: io.connect(process.env.API_URL),
    user,
    game,
    messages,
    lang,
    gameDisplay: 0,
    currentBoard: 0,
    lastCell: '',
    barMenuOpened: 0,
    text: '',
    tab: 1,
    redirect: 0,
    console: ''
  },
  mutations: {
    /**
     common
     */
    set (state, { type, items }) {
      state[type] = items
    },
    lsUser () {
      Vue.ls.set('user', user)
    },
    lsGame () {
      Vue.ls.set('game', game)
    },
    lsMessages () {
      Vue.ls.set('messages', messages)
    },
    setLastCell (state, index) {
      state.lastCell = index
    },
    setText (state, text) {
      state.text = text
    },
    setMessage (state, msg = {}) {
      let newmsg = new Message(msg, game)
      for (let i in state.messages) {
        delete state.messages[i].pop0
        delete state.messages[i].pop1
      }
      state.messages[newmsg._id] = newmsg
      state.messages.c = Object.keys(state.messages).length
      this.commit('lsMessages')
    },
    clearMessages (state) {
      state.messages = messages = {c: 0}
      this.commit('lsMessages')
    },
    setBarMenuOpen (state, isOpened) {
      state.barMenuOpened = (isOpened !== 'toggle') ? isOpened : !state.barMenuOpened
    },
    setTab (state, tab = false) {
      state.tab = tab
    },
    setBoard (state, i = 0) {
      state.currentBoard = +i
    },
    setRedirect (state, redirect) {
      state.redirect = redirect
    },
    setGameDisplay (state) {
      let gd = state.gameDisplay = game.display
      gd.width = document.documentElement.clientWidth
      gd.height = document.documentElement.clientHeight
      gd.land = (gd.width > gd.height) ? 1 : 0
      gd.boardWidth = (gd.land) ? gd.height : gd.width
      if (gd.boardWidth > gd.boardMaxWidth) gd.boardWidth = gd.boardMaxWidth
      gd.cellWidth = gd.boardWidth / (game.boardIndexes.row.length + 1)
      if (gd.width > gd.boardWidth * 2 + 20) gd.land = 2
      gd.mainHeight = gd.height
      let bar = document.getElementById('Bar')
      if (bar) gd.mainHeight -= bar.offsetHeight
      if (gd.timer) clearTimeout(gd.timer)
      gd.timer = setTimeout(() => {
        let send = document.getElementById('Send')
        if (send) gd.mainHeight -= send.offsetHeight
      }, 1000)
    },
    gameLog (state, text = '') {
      state.console = text + '\r\n' + state.console
      // this.commit('setMessage', {body: text})
    },
    switchLang (state, language = false) {
      if (language && (language === state.lang)) {
        return
      }
      if (!language) {
        language = (state.lang === 'en') ? 'ru' : 'en'
      }
      state.lang = user.lang = language
      Vue.ls.set('lang', language)
      this.dispatch('changePlayerInfo', {field: 'lang', val: language})
      window.location.reload(true)
    },
    /**
     sockets
     */
    setSocket (state) {
      state.io
        .on('authenticated', () => {
          console.log('Socket authenticated.')
          this.commit('gameLog', 'Socket authenticated.')
          this.dispatch('getUser')
        })
        .on('unauthorized', (msg) => {
          if (msg.data.message === 'jwt expired') {
            this.dispatch('refreshToken')
          }
        })
        .on('connect', () => {
          game.__v--
          this.commit('gameLog', 'Socket connected.')
          let lastMsg = Object.values(messages).pop()
          if (lastMsg && lastMsg.type && (lastMsg.type === 'error')) {
            this.commit('setMessage', {body: t('üëå Socket connected.')})
          }
          this.commit('authSocket')
          if (!io.queue.length) {
            this.dispatch('getGame')
            return
          }
          this.dispatch('setIoQueue')
        })
        .on('connect_error', () => {
          game.__v--
          this.commit('gameLog', 'Socket error.')
          let lastMsg = Object.values(messages).pop()
          if (lastMsg && lastMsg.type && (lastMsg.type === 'error')) return
          this.commit('setMessage', {type: 'error', body: t('üëé Socket error.')})
        })
        .on('MESSAGE', (msg) => {
          this.commit('gameLog', String(msg))
        })
        .on('USER_REGISTERED', (item) => {
          if (item !== user._id) {
            game.player(1).user.connect = true
            game.__v--
            this.commit('gameLog', 'User1 online')
          }
        })
        .on('USER_DISCONNECTED', () => {
          game.player(1).user.connect = false
          game.__v--
          this.commit('gameLog', 'User1 offline')
        })
        .on('USER_READY', (item) => {
          if (item._id !== user._id) {
            game.player(1).phase = 'ready'
            game.boat(1).c = 100
            game.__v--
            this.commit('lsGame')
          }
          this.commit('gameLog', item.name + ' ready.')
          this.commit('setMessage', {userid: item._id, body: t('üç∏ Ready.')})
          if (game.phase('ready')) {
            this.commit('setMessage', {userid: game.playerId1, body: t('üí™ Let the fight begin!')})
          }
        })
        .on('USER_CHANGED', (item) => {
          let namePrev = game.user1.name
          game.user1 = item
          game.__v = item.__v
          this.commit('lsGame')
          this.commit('gameLog', namePrev + ' now known as ' + item.name + '.')
          this.commit('setMessage', {userid: item._id, body: 'üë§ ' + namePrev + ' ' + t('now known as') + ' ' + item.name + '.'})
        })
        .on('USER_CHANGED_NAME', (item) => {
          let namePrev = game.user1.name
          game.player(1).user.name = item.name
          game.__v = item.__v
          this.commit('lsGame')
          this.commit('gameLog', namePrev + ' now known as ' + item.name + '.')
          this.commit('setMessage', {userid: item._id, body: 'üë§ ' + namePrev + ' ' + t('now known as') + ' ' + item.name + '.'})
        })
        .on('GAME_CREATED', () => {
          game.__v = 1
          this.commit('lsGame')
          this.commit('gameLog', 'Game saved in DB.')
        })
        .on('GAME_NOTFOUND', () => {
          this.commit('gameLog', 'Game ' + game._id + ' not found.')
          this.dispatch('newGame')
        })
        .on('GAME_DISPOSITION_SAVED', (item) => {
          game.__v = item
          this.commit('lsGame')
        })
        .on('GAME_GOT', (item) => {
          this.commit('gotGame', item)
          this.commit('gameLog', 'Game got.')
        })
        .on('GAME_JOINED0', (item) => {
          this.commit('gotGame', item)
          this.commit('gameLog', t('üîó Joined.'))
          this.commit('setMessage', {userid: user._id, body: t('üîó Joined.')})
        })
        .on('GAME_JOINED1', (item) => {
          this.commit('gotGame', item)
          this.commit('gameLog', game.user1.name + ' ' + t('üîó Joined.'))
          this.commit('setMessage', {userid: game.playerId1, body: t('üîó Joined.')})
        })
        .on('GAME_MOVE', (item) => {
          this.commit('moveGame', item)
        })
        .on('GAME_CHALLENGE', (item) => {
          this.commit('setMessage', {userid: item, type: 'challenge', body: t('üí£ New challenge')})
        })
        .on('MSG_RECEIVE', (item) => {
          this.dispatch('receiveMessage', item)
        })
        .on('MSG_RECEIVED', (item) => {
          state.messages[item].loading = false
          Vue.ls.set('messages', messages)
        })
    },
    authSocket (state) {
      let token = (user.tokens && user.tokens.at) ? user.tokens.at : null
      state.io.emit('authenticate', {token})
    },
    /**
     game
     */
    gotGame (state, item) {
      game._id = item._id
      game.turn = item.turn
      game.__v = item.__v
      game.players = item.players
      game.playersIDs = item.playersIDs
      game.newgame = item.newgame
      this.commit('lsGame')
    },
    moveGame (state, item) {
      game.turn = item.turn
      game.__v = item.__v
      if (!item.changes) return
      if (item.changes.board) {
        for (let i in item.changes.board) {
          let changes = item.changes.board[i]
          for (let k = 0; k < changes.length; k++) {
            game.players[item.secondid].board[i[0]][i[1]][changes[k][0]] = changes[k][1]
          }
        }
        this.commit('setMessage', {userid: item.userid, body: game.boardIndexes.col[item.index[1]].toUpperCase() + game.boardIndexes.row[item.index[0]] + ' <i>‚Üí</i> ' + t(item.class) + '.'})
        let playerindex = +(game.playerId1 === item.secondid)
        this.commit('setLastCell', '' + playerindex + item.index[0] + item.index[1])
      }
      if (item.changes.boat) {
        if (item.changes.boat.n) {
          let changes = item.changes.boat.n
          game.players[item.secondid].boat.n[changes[0]][1] = changes[1]
        }
        if (item.changes.boat.c) {
          game.players[item.secondid].boat.c = item.changes.boat.c
        }
      }
      if (item.newgame) {
        game.newgame = item.newgame
        this.dispatch('endGame', item.userid)
      }
      this.commit('lsGame')
      this.commit('chekTab')
    },
    chekTab (state) {
      if (!state.tab && (game.turn === user._id)) {
        this.dispatch('notify', t('Your turn!'))
      }
    }
  },
  actions: {
    /**
     common
     */
    notify (context, text) {
      Push.create(text, {
        // body: 'Hows it hangin?',
        icon: '/static/apple-touch-icon.png',
        timeout: 10000,
        onClick: function () {
          window.focus()
          this.close()
        }
      })
    },
    setIoQueue (context, {method, tosend} = {}) {
      if (!context.state.io.connected) {
        io.queue.push({method, tosend})
        context.commit('gameLog', 'Queue: ' + method + '.')
        return
      }
      let gameid = game._id || null
      let user1 = game.user1._id || null
      context.state.io.emit('register', gameid, user._id, user1)
      if (method && tosend) {
        context.state.io.emit(method, tosend)
        return
      }
      while (io.queue.length) {
        let {method, tosend} = io.queue.pop()
        context.state.io.emit(method, tosend)
      }
    },
    /**
     user
     */
    createUser (context) {
      context.state.user = user = new User(true)
      user.lang = context.state.lang
      context.commit('lsUser')
    },
    getUser ({commit, dispatch}) {
      if (user.__v <= 0) {
        return false
      }
      dispatch('checkToken')
        .then((response) => {
          if (response) {
            const url = process.env.API_URL + '/auth/get'
            let tosend = {
              userid: user._id
            }
            return axios.put(url, tosend)
              .then((response) => {
                user.name = response.data.name
                user.email = response.data.email
                user.__v = response.data.__v
                commit('lsUser')
              })
              .catch((error) => {
                if (error.response && error.response.data) {
                  commit('setText', error.response.data)
                } else {
                  console.log(error)
                }
              })
          }
        })
        .catch(() => {
          return false
        })
    },
    setUserPhase ({commit, dispatch}, n) {
      commit('setText', '')
      user.phase = n
      if (n === 'signed') {
        commit('authSocket')
      } else {
        dispatch('deleteToken')
      }
      commit('lsUser')
    },
    signIn ({commit, dispatch}, {email, password}) {
      const url = process.env.API_URL + '/auth/signin'
      user.email = email
      commit('lsUser')
      let tosend = {
        userid: user._id,
        name: user.name,
        lang: user.lang,
        email,
        password
      }
      return axios.put(url, tosend)
        .then((response) => {
          commit('setText', response.data)
          user._id = response.data._id
          user.name = response.data.name
          user.email = response.data.email
          user.__v = response.data.__v
          user.tokens = response.data.tokens
          dispatch('changePlayer')
          dispatch('setUserPhase', 'signed')
          commit('switchLang', response.data.lang)
        })
        .catch((error) => {
          if (error.response && error.response.data) {
            if (error.response.data === 'emailNotVerified') {
              dispatch('setUserPhase', 'secret')
            } else {
              commit('setText', error.response.data)
            }
          } else {
            console.log(error)
          }
        })
    },
    verifyEmail ({commit, dispatch}, secret) {
      const url = process.env.API_URL + '/auth/verifyemail'
      let tosend = {
        userid: user._id,
        email: user.email,
        secret
      }
      return axios.put(url, tosend)
        .then(() => {
          dispatch('setUserPhase', 'emailVerified')
        })
        .catch((error) => {
          commit('setText', error.response.data)
        })
    },
    changePlayerInfo ({commit, dispatch}, {field, val}) {
      dispatch('checkToken')
        .then((response) => {
          if (response) {
            const url = process.env.API_URL + '/auth/edit'
            user[field] = val
            commit('lsUser')
            let tosend = {
              userid: user._id,
              field,
              val
            }
            return axios.put(url, tosend)
              .then(() => {
                if (field === 'name') {
                  dispatch('changePlayerName')
                }
              })
              .catch((error) => {
                if (error.response && error.response.data) {
                  if (error.response.data === 'emailNotVerified') {
                    dispatch('setUserPhase', 'secret')
                  } else {
                    commit('setText', error.response.data)
                  }
                } else {
                  console.log(error)
                }
              })
          }
        })
        .catch(() => {
          return false
        })
    },
    checkValidInfo (context, {field, val}) {
      const url = process.env.API_URL + '/auth/check'
      let tosend = {
        field,
        val
      }
      return axios.put(url, tosend)
    },
    checkToken ({commit, dispatch}) {
      const url = process.env.API_URL + '/auth/checktoken'
      if (!user.tokens || !user.tokens.at) {
        return false
      }
      let tosend = {
        userid: user._id,
        token: user.tokens.at
      }
      return axios.put(url, tosend)
        .then(() => {
          return true
        })
        .catch((error) => {
          if (error.response && error.response.data) {
            if (error.response.data === 'TokenExpiredError') {
              return dispatch('refreshToken')
            } else {
              dispatch('createUser')
              dispatch('changePlayer')
            }
          } else {
            console.log(error)
          }
          return false
        })
    },
    refreshToken ({commit, dispatch}) {
      const url = process.env.API_URL + '/auth/refreshtoken'
      if (!user.tokens || !user.tokens.rt) {
        return
      }
      let tosend = {
        userid: user._id,
        token: user.tokens.rt
      }
      commit('setText', t('üì° Connecting...'))
      return axios.put(url, tosend)
        .then((response) => {
          commit('setText', '')
          user.tokens = response.data.tokens
          dispatch('setUserPhase', 'signed')
        })
        .catch((error) => {
          commit('setText', '')
          dispatch('createUser')
          dispatch('changePlayer')
          if (error.response && error.response.data) {
            if (error.response.data === 'TokenNotFoundError') {
              commit('setText', 'Invalid token.')
            } else if (error.response.data === 'TokenExpiredError') {
              commit('setText', 'Token Expired.')
            } else {
              commit('setText', error.response.data)
            }
          } else {
            console.log(error)
          }
        })
    },
    deleteToken ({commit}, all = false) {
      const url = process.env.API_URL + '/auth/deletetoken'
      if (!user.tokens && !all) {
        return
      }
      let tosend = {
        userid: user._id
      }
      if (all) {
        tosend.all = all
      } else {
        tosend.token = user.tokens.rt
      }
      user.tokens = null
      return axios.put(url, tosend)
    },
    /**
     game
     */
    createGame (context) {
      context.commit('gameLog', 'v: ' + process.env.APP_VERSION)
      if (game.__v) {
        context.dispatch('getGame', true)
        return
      }
      let tosend = {
        gameid: game._id,
        userid: user._id,
        name: user.name
      }
      if (game.playerId1) {
        tosend.challenge = game.playerId1
        game.user1 = new User()
      }
      context.commit('lsGame')
      context.dispatch('setIoQueue', {method: 'createGame', tosend})
      context.commit('gameLog', 'Game: ' + game._id + '.')
      context.commit('clearMessages')
      context.commit('setMessage', {type: 'newgame', body: t('üéÆ New game:') + ' ' + game._id + '.'})
    },
    newGame (context, id = false) {
      context.commit('setText', '')
      context.commit('setBoard', 0)
      context.commit('setLastCell', '')
      context.state.game = game = new Game(user, game.user1)
      if (id) {
        game._id = id
      } else {
        if (game.playerId1) {
          game.user1 = new User()
        }
      }
      context.commit('clearMessages')
      context.commit('gameLog', 'New game: ' + game._id + '.')
      context.dispatch('createGame')
    },
    getGame (context, force = false) {
      let tosend = {
        gameid: game._id,
        userid: user._id
      }
      if (!force) {
        tosend.__v = game.__v
      }
      context.dispatch('setIoQueue', {method: 'getGame', tosend})
      context.commit('gameLog', 'Get.')
    },
    disposition ({dispatch}, index) {
      game.disposition(index)
      dispatch('saveGameDisposition')
    },
    autoDisposition ({dispatch}) {
      game.autoDisposition()
      dispatch('saveGameDisposition')
    },
    clearDisposition ({dispatch}) {
      game.clearDisposition()
      dispatch('saveGameDisposition')
    },
    finishDisposition ({dispatch}) {
      game.finishDisposition()
      dispatch('saveGameDisposition')
    },
    saveGameDisposition (context) {
      context.commit('lsGame')
      let tosend = {
        gameid: game._id,
        userid: user._id,
        player: game.player()
      }
      context.dispatch('setIoQueue', {method: 'saveGameDisposition', tosend})
    },
    endGame ({commit}, userid) {
      let winner = game.players[userid].user
      commit('setText', winner.name + ' ' + t('üëë üòé üéá üèÜ Won!'))
      commit('setMessage', {userid, body: t('üëë üòé üéá üèÜ Won!')})
    },
    move (context, index) {
      if (game.turn === user._id) {
        let cell = game.cell(index, 1, true)
        if (!cell.class) {
          game.turn += 'move'
          cell.class = 'loading'
          let tosend = {
            _id: game._id,
            userid: user._id,
            secondid: game.playerId1,
            index
          }
          context.dispatch('setIoQueue', {method: 'moveGame', tosend})
        }
      }
    },
    changePlayer (context) {
      let userPrev = game.playerId0
      if (userPrev !== user._id) {
        game.user0 = user
        game.__v++
        context.commit('lsGame')
      }
      let tosend = {
        gameid: game._id,
        userprev: userPrev,
        userid: user._id,
        name: user.name
      }
      context.dispatch('setIoQueue', {method: 'changePlayer', tosend})
    },
    changePlayerName (context) {
      game.player().user.name = user.name
      context.commit('lsGame')
      let tosend = {
        gameid: game._id,
        userid: user._id,
        name: user.name
      }
      context.dispatch('setIoQueue', {method: 'changePlayerName', tosend})
    },
    sendMessage (context, body) {
      body = body.replace(new RegExp('\\n', 'g'), '<br>')
      let msg = new Message({userid: user._id, body, direct: true}, game)
      context.commit('setMessage', msg)
      let tosend = {
        msgid: msg._id,
        userid: user._id,
        body,
        direct: true,
        user1: game.playerId1
      }
      context.dispatch('setIoQueue', {method: 'sendMessage', tosend})
    },
    receiveMessage (context, item) {
      let {msgid = null, userid = null, body = null, direct = false} = item
      context.commit('setMessage', {_id: msgid, userid, body, direct, loading: false})
      let tosend = {
        msgid,
        userid,
        received: true
      }
      context.dispatch('setIoQueue', {method: 'sendMessage', tosend})
    }
  }
})

export default store

if (!User.isValid(user)) {
  game = false // anyway reinit game for new players
  store.dispatch('createUser')
}

if (Game.isValid(game, user)) {
  game = new Game(null, null, game)
} else {
  game = new Game(user, new User())
  store.commit('lsGame')
}
store.state.game = game
store.commit('setSocket')

