import Vue from 'vue'
import Router from 'vue-router'
import Basic from '@/components/Basic'
import User from '@/components/User'
import Game from '@/components/Game'
import Rules from '@/components/Rules'
import About from '@/components/About'
import Console from '@/components/Console'
import VueHead from 'vue-head'

Vue.use(Router)
Vue.use(VueHead)

const router = new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      meta: {
        title: 'Basic'
      },
      component: Basic
    },
    {
      path: '/user',
      meta: {
        title: 'Captain'
      },
      component: User
    },
    {
      path: '/verify/:userId/:code',
      meta: {
        title: 'Captain'
      },
      component: User
    },
    {
      path: '/g/:joinGameId',
      meta: {
        title: 'Join Game'
      },
      component: Game
    },
    {
      path: '/game',
      meta: {
        title: 'Game'
      },
      component: Game
    },
    {
      path: '/rules',
      meta: {
        title: 'Rules'
      },
      component: Rules
    },
    {
      path: '/about',
      meta: {
        title: 'About'
      },
      component: About
    },
    {
      path: '/console',
      meta: {
        title: 'Console'
      },
      component: Console
    }
  ]
})

export default router



