<template>
  <div>
    <div class="Rules plr">
      <h2>{{ t('Battleship') }}</h2>
      <div v-html="t('Rules01')"></div>

      <h2 id="ships">{{ t('Ships') }}</h2>
      <table>
        <tr>
          <th>
            {{ t('Type of ship') }}
          </th>
          <th>
            {{ t('Quantity') }}
          </th>
        </tr>
        <tr>
          <td>
            {{ t('Battleship (four-decker)') }}
          </td>
          <td class="center">
            1
          </td>
        </tr>
        <tr>
          <td>
            {{ t('Cruiser (three-decker)') }}
          </td>
          <td class="center">
            2
          </td>
        </tr>
        <tr>
          <td>
            {{ t('Destroyer (two-decker)') }}
          </td>
          <td class="center">
            3
          </td>
        </tr>
        <tr>
          <td>
            {{ t('Submarine (single-decker)') }}
          </td>
          <td class="center">
            4
          </td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
import { t } from 'localizify'
export default {
  name: 'Rules',
  data () {
    return {
      t
    }
  }
}
</script>
<style lang="sass">
  .Rules
    padding-top: 1em;
    padding-bottom: 1em;

</style>



