<template>
  <transition name="show">
    <div class="Text plr" v-if="text">
        <div class="Text-text">{{ text }}</div>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'Texts',
  data () {
    return {}
  },
  computed: {
    text: function () {
      return this.$store.state.text
    }
  }
}
</script>

<style lang="sass">
  .Text-text
    margin: 1em 0 0;
    text-align: center;
    color: #666;
    white-space: pre-line;

</style>



