<template>
  <div>
    <div class="About plr">
      <p v-html="t('DeveloperInfo')"></p>
      <p>
        <b>{{ t('Frontend:') }}</b>
        <br>
        Vue,
        Vuex,
        Socket.io,
        Webpack
        <br>
        Clone,
        Es6-promise,
        Eslint,
        Push.js,
        Shortid,
        Vue-flickity,
        Vue-ls,
        Vue-pull-refresh,
        Vue-router
      </p>
      <p>
        <b>{{ t('Backend:') }}</b>
        <br>
        Heroku.com,
        Mlab.com,
        Express,
        Mongoose
        <br>
        Cors,
        Socket.io
      </p>
    </div>
  </div>
</template>

<script>
import { t } from 'localizify'
export default {
  name: 'About',
  data () {
    return {
      t
    }
  }
}
</script>
<style lang="sass">
  .About
    padding-top: 1em;
    padding-bottom: 1em;
    box-sizing: border-box;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;

</style>



