<template>
  <div class="Bar" id="Bar">
    <div class="plr">
      <router-link to="/" class="Bar__logo"></router-link>
      <div class="Bar-title">{{ title }}</div>
      <div class="Bar-menu">
        <div class="Bar-menu__icon" @click="switchMenu()"></div>
      </div>
    </div>
    <flickity class="Bar-menu-panel-flickity" ref="flickity" :options="flickityOptions" :style="flickityStyle">
      <div class="Bar-menu-panel plr">
        <div class="Bar-menu__item"><i>üë§</i> <router-link to="/user/">{{ captain }}</router-link></div>
        <div class="Bar-menu__item"><i>üéÆ</i> <router-link to="/game/">{{ t('Game') }}</router-link></div>
        <div class="Bar-menu__item"><i>üìë</i> <router-link to="/rules/">{{ t('Rules') }}</router-link></div>
        <div class="Bar-menu__item"><i>üîå</i> <router-link to="/console/">Console</router-link></div>
        <div class="Bar-menu__item"><i>¬©</i> <router-link to="/about/">About</router-link></div>
        <div class="Bar-menu__line"></div>
        <div class="Bar-menu__item" @click="switchLang"><i>üòõ</i> {{ t('In English') }}</div>
      </div>
    </flickity>
  </div>
</template>
<script>
import Flickity from 'vue-flickity'
import Notify from '@/components/Notify'
import { t } from 'localizify'
export default {
  name: 'Bar',
  data () {
    return {
      t,
      flickityOptions: {
        draggable: true,
        freeScroll: true,
        contain: true,
        prevNextButtons: false,
        pageDots: false,
        cellAlign: 'left'
      },
      flickityVector: 'init'
    }
  },
  computed: {
    title: function () {
      if (!this.$route.meta.title) return ''
      return t(this.$route.meta.title)
    },
    captain: function () {
      return (this.$store.state.user.__v) ? this.$store.state.user.name : t('Captain')
    },
    flickityStyle: function () {
      if (this.flickityVector > 0) {
        let delta = 0.7 - this.flickityVector / this.$store.state.gameDisplay.width * 0.7
        return 'background: rgba(60,120,120, ' + delta + ');'
      } else if (this.flickityVector === 'init') {
        return 'opacity:0;'
      } else {
        return ''
      }
    }
  },
  mounted () {
    this.$nextTick(function () {
      let that = this
      this.resetFlickityVector()
      this.$refs.flickity.on('dragMove', function (event, pointer, moveVector) {
        that.flickityVector = moveVector.x
      })
      this.$refs.flickity.on('dragEnd', function (event, pointer) {
        if (that.flickityVector > 0) {
          that.closeMenu()
        }
      })
      this.$refs.flickity.on('staticClick', function (event, pointer, cellElement, cellIndex) {
        if (typeof cellIndex === 'undefined') {
          that.closeMenu()
        }
      })
    })
  },
  methods: {
    switchLang () {
      this.$store.commit('switchLang')
    },
    switchMenu (isOpened = true) {
      this.$store.commit('setBarMenuOpen', isOpened)
      this.resetFlickityVector()
    },
    closeMenu () {
      this.switchMenu(false)
    },
    resetFlickityVector () {
      let that = this
      setTimeout(function () {
        that.flickityVector = 0
      }, 490)
    }
  },
  components: {
    Flickity,
    Notify
  }
}
</script>
<style lang="sass" >
  .Bar
    height: calc(3rem + 20px);
    box-sizing: border-box;
    background: $blue;
    padding-top: calc(.5rem + 20px);
    padding-bottom: .5rem;
    z-index: 98;

  .Bar-title
    text-align: center;
    line-height: 2.1rem;
    font-size: 1.3rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding: 0 12vw;
    color: #fff;

  .home .Bar-title
    opacity: 0;

  .Bar__logo
    position: absolute;
    width: 2rem;
    height: 2rem;
    background: 100% 50% no-repeat;
    background-image: $img-logo;
    background-size: cover;
    transform-origin: 50% 50%;
    transition: all .1s ease;
    margin-left: 1.3rem;

  .Bar__logo:before
    content: '';
    width: 1rem;
    height: 1.5rem;
    background: #ccc;
    position: absolute;
    left: -1.3rem;
    background: 0% 50% no-repeat;
    background-image: $img-left;
    background-size: contain;

  .home .Bar__logo
    width: 3.5rem;
    margin-left: 0;

  .home .Bar__logo:before
    display: none;

  .Bar__logo:active
    transform: scale(.8);

  .Bar-menu
    position: absolute;
    top: calc(.5rem + 20px);
    right: 4vw;

  .Bar-menu__icon
    display: block;
    width: 2rem;
    height: 2rem;
    background: 50% 50% no-repeat;
    background-image: $img-helm;
    background-size: contain;
    cursor: pointer;
    transform-origin: 50% 50%;
    transition: all .2s ease;

  .Bar-menu__icon:active
    transform: scale(.8) rotate(20deg);

  @media screen and (min-width: 1020px)
    .Bar
      height: 5rem;
      padding-top: .5rem;

    .Bar__logo, .Bar-menu__icon
      width: 60px;
      height: 60px;

    .Bar-title
      line-height: 70px;
      font-size: 2.4em;

    .Bar__logo
      background-size: 100px;
      margin-left: 50px;

    .Bar__logo:before
      width: 30px;
      height: 60px;
      left: -50px;

    .home .Bar__logo
      width: 110px;
      margin-left: 0;

    .Bar-menu
      right: 30px;
      top: .5rem;

  .Bar-menu-panel
    box-sizing: border-box;
    padding-top: 1em;
    padding-bottom: .5em;
    background: #fff;
    width: 300vw;
    min-height: 100vh;
    left: calc(100vw - 280px) !important;

  .Bar-menu__line
    border-bottom: 1px solid #ddd;
    margin: 1.5em 0;
    width: 220px;

  .Bar-menu__item
    display: block;
    margin-bottom: .5em;
    font-weight: 500;
    transform-origin: 0% 50%;
    transition: all .2s ease;

  .Bar-menu__item i
    font-style: normal;
    display: inline-block;
    width: 1em;
    text-align: center;

  .Bar-menu__item a
    text-decoration: none;
    color: #000;

  .Bar-menu__item:active
    transform: scale(.8);

  .Bar-menu__item.router-link-active
    color: #999;
    text-decoration: underline;

  .Bar-menu-panel-flickity
    position: fixed;
    width: 100vw;
    top: 0;
    right: 0;
    user-select: none;
    outline: none !important;
    background: rgba(60,120,120,.7);
    transition: background .7s ease;
    z-index: 99;

  .Bar-menu-panel-flickity .flickity-viewport
    height: 100vh !important;
    overflow-y: auto;
    overflow-x: hidden;

  .Bar-menu-panel-open1 .Bar-menu-panel-flickity
    animation: bar-menu-panel-in1 .5s ease forwards;

  @keyframes bar-menu-panel-in1
    0%
      background: rgba(60,120,120,0);

  .Bar-menu-panel-open1 .Bar-menu-panel-flickity .flickity-slider
    animation: bar-menu-panel-in .5s ease forwards;

  @keyframes bar-menu-panel-in
    0%
      transform: translateX(100%);

  .Bar-menu-panel-open0 .Bar-menu-panel-flickity
    animation: bar-menu-panel-out .1s ease .5s forwards, bar-menu-panel-out1 .5s ease forwards;

  @keyframes bar-menu-panel-out
    100%
      right: 100vw;

  @keyframes bar-menu-panel-out1
    100%
      background: rgba(60,120,120,0);

  .Bar-menu-panel-open0 .Bar-menu-panel-flickity .flickity-slider
    animation: bar-menu-panel-out2 .5s ease forwards;

  @keyframes bar-menu-panel-out2
    100%
      transform: translateX(100%);

</style>



