<template>
  <div>
    <transition name="show">
      <div class="Notify">
        <div class="form">
          <div class="Notify-msg">
            Capitan Flint
            <br>
            challenged for a new round.
          </div>
          <div class="Notify-btns">
            <button class="" @click="accept">Accept</button>
            <button class="deny" @click="deny">Deny</button>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'Notify',
  data () {
    return {
    }
  },
  computed: {
    console: function () {
      return this.$store.state.console
    }
  },
  methods: {
    accept: function () {
    },
    deny: function () {
    }
  }
}
</script>
<style lang="sass" scoped>
  .Notify
    padding: 2em 1em;
    background: $grey1;
    text-align: center;
    display: none;

  .Notify-msg
    font-size: 1.2em;

  .Notify-btns
    margin: 1.5em 0 0;

  button+button
    margin-top: 1em;
</style>



