<template>
  <div>
    <div class="Console plr">
      <pre>{{console}}</pre>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Console',
  data () {
    return {
    }
  },
  computed: {
    console: function () {
      return this.$store.state.console
    }
  }
}
</script>
<style lang="sass" scoped>
  .Console
    padding-top: 1em;
    padding-bottom: 1em;
    font-size: .8em;

</style>



