import shortid from 'shortid'
import { t } from 'localizify'

export default class Message {
  constructor (msg = {}, game) {
    this._id = msg._id || shortid.generate()
    this.time = msg.time || Date.now()
    this.userid = (typeof msg.userid !== 'undefined') ? msg.userid : 'system'
    this.direct = msg.direct || false
    this.loading = msg.loading || null
    this.type = msg.type || ''
    this.body = msg.body || ''
    this.pop0 = msg.pop0 || true
    this.pop1 = msg.pop1 || false

    if ((this.userid !== game.user0._id) && (this.userid !== 'system')) {
      this.left = true
    }
    let time = new Date(this.time)
    time.hours = time.getHours()
    time.minutes = time.getMinutes()
    if (time.hours < 10) time.hours = '0' + time.hours
    if (time.minutes < 10) time.minutes = '0' + time.minutes
    this.timehm = time.hours + ':' + time.minutes

    if (this.userid !== 'system') {
      let player = game.players[this.userid]
      if (player) {
        this.name = player.user.name
      }
    }

    if (game.newgame && this.type && (this.type === 'challenge')) {
      this.body += ' <a href="' + game.link(game.newgame) + '" class="button button-small-red">' + t('Accept') + '</a>'
    }

    if (this.direct && (typeof msg.loading === 'undefined')) {
      this.loading = true
    }
  }
}



