<template>
  <div id="App" :class="appClass">
    <bar/>
    <vue-pull-refresh :on-refresh="onRefresh" class="mainview" id="mainview" :style="mainHeight">
      <transition :name="transitionName">
          <router-view :user0connect="user0connect" :user1connect="user1connect"/>
      </transition>
    </vue-pull-refresh>
    <transition name="show-del2">
      <send v-if="home" :user0connect="user0connect" :user1connect="user1connect"/>
    </transition>
  </div>
</template>

<script>
import Bar from '@/components/Bar'
import VuePullRefresh from 'vue-pull-refresh'
import Send from '@/components/Send'
let time = new Date()
export default {
  name: 'App',
  data () {
    return {
      transitionName: 'slide-left',
      user0connect: false,
      user1connect: false,
      refresh: false
    }
  },
  head: {
    meta: [
      {'http-equiv': 'last-modified', content: time},
      {name: 'Etag', content: time}
    ]
  },
  computed: {
    home: function () {
      return (this.$route.path === '/')
    },
    mainHeight: function () {
      return 'height:' + this.$store.state.gameDisplay.mainHeight + 'px;'
    },
    appClass: function () {
      return 'land' + this.$store.state.gameDisplay.land +
        ' Bar-menu-panel-open' + +this.$store.state.barMenuOpened +
        ((this.home) ? ' home' : '') +
        ((this.refresh) ? ' refresh' : '')
    },
    pullStyle: function () {
      return 'height:' + this.$store.state.gameDisplay.height + 'px;'
    },
    version: function () {
      return this.$store.state.game.__v
    }
  },
  components: {
    Bar,
    'vue-pull-refresh': VuePullRefresh,
    Send
  },
  methods: {
    windowResized () {
      this.$store.commit('setGameDisplay')
    },
    closeMenu () {
      this.$store.commit('setBarMenuOpen', false)
    },
    tabFocus () {
      this.$store.commit('setTab', true)
    },
    tabBlur () {
      this.$store.commit('setTab', false)
    },
    onRefresh () {
      this.refresh = true
      window.location.reload(true)
    },
    watchVersion () {
      this.user0connect = this.$store.state.io.connected
      this.user1connect = this.$store.state.game.user1.connect
    }
  },
  watch: {
    '$route' (to, from) {
      this.windowResized()
      this.closeMenu()
      const toDepth = to.path.split('/').length
      const fromDepth = from.path.split('/').length
      this.transitionName = toDepth < fromDepth ? 'slide-right' : 'slide-left'
      setTimeout(() => {
        document.getElementById('mainview').scrollTop = 0
      }, 250)
    },
    version () {
      this.watchVersion()
    }
  },
  beforeCreate () {
    if (~this.$route.fullPath.indexOf('%C2%AD')) {
      let path = this.$route.fullPath.replace(new RegExp('%C2%AD', 'g'), '')
      this.$router.push(path)
    }
  },
  created () {
    let joinGameId = this.$route.params.joinGameId
    if (joinGameId && (joinGameId !== this.gameId)) {
      this.$store.commit('setRedirect', true)
      this.$store.dispatch('newGame', joinGameId)
    } else {
      this.$store.dispatch('createGame')
    }
    this.watchVersion()
  },
  mounted () {
    this.$nextTick(function () {
      this.windowResized()
      window.addEventListener('resize', this.windowResized)
      window.addEventListener('focus', this.tabFocus)
      window.addEventListener('blur', this.tabBlur)
      let appStart = document.getElementById('app-start')
      if (appStart) {
        appStart.className = 'out'
        setTimeout(() => {
          appStart.className = 'del'
        }, 3000)
        // appStart.parentNode.removeChild(appStart)
      }
    })
  },
  beforeDestroy () {
    window.removeEventListener('resize', this.windowResized)
    window.removeEventListener('focus', this.tabFocus)
    window.removeEventListener('blur', this.tabBlur)
  }
}
</script>
<style lang="sass">
  *
    -webkit-touch-callout: none;                /* prevent callout to copy image, etc when tap to hold */
    -webkit-text-size-adjust: none;             /* prevent webkit from resizing text to fit */
    -webkit-tap-highlight-color: rgba(0,0,0,0); /* prevent tap highlight color / shadow */
    line-height: 1.2em;

  html, body
    overflow: hidden;

  #App
    font: 16px monospace;
    position: relative;
    width: 100vw;
    min-height: 100vh;
    box-sizing: border-box;
    transition: all .5s ease;
    overflow: hidden;
    color: #444;

  a
    color: $blue;

  .mainview
    background: #fff;
    height: 100vh;
    box-sizing: border-box;
    overflow-x: hidden;
    overflow-y: scroll;
    scroll-behavior: smooth;

  .slide-left-enter-active, .slide-left-leave-active, .slide-right-enter-active, .slide-right-leave-active
    transition: all .5s ease-in-out;
    position: absolute;
    background: #fff;
    width: 100vw;
    min-height: 100vh;
    transform: translateX(0);

  .slide-left-enter-active, .slide-right-enter-active
    z-index: 11;

  .slide-left-leave-active:after, .slide-right-leave-active:after
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    min-height: 100%;
    z-index: 10;
    background: rgba(60,120,120,.7);
    opacity: 0;
    transition: all .5s ease;

  .slide-left-enter
    transform: translateX(100%);

  .slide-right-enter
    transform: translateX(-100%);

  .slide-left-leave-to:after, .slide-right-leave-to:after
    opacity: 1;

  .plr
    padding-left: 4vw;
    padding-right: 4vw;

  @media screen and (min-width: 1020px)
    .plr
      padding-left: 30px;
      padding-right: 30px;

  .form
    box-sizing: border-box;
    max-width: 320px;
    margin-left: auto;
    margin-right: auto;

  .form-signin
    margin-top: calc(4vw + 1em);
    background: $grey1;
    outline: 4vw solid $grey1;

  @media screen and (min-width: 1020px)
    .form-signin
      margin-top: 60px;
      outline-width: 30px;

  .form-row
    margin-bottom: 1em;

  .buttons
    margin-top: 1.5em;

  input, textarea
    width: 100%
    box-sizing: border-box;
    padding: .5em;
    border-radius: 3px;
    border: 1px solid #ccc;
    transition: all .1s ease;
    outline: none !important;

  .button, button
    display: inline-block;
    width: 100%;
    border: 0;
    border-radius: 3px;
    background: $blue;
    box-sizing: border-box;
    padding: .5em 1em;
    color: #fff;
    text-align: center;
    text-decoration: none;
    font-weight: bold;
    cursor: pointer;
    user-select: none;
    transform-origin: 50% 50%;
    transition: all .1s ease;
    outline: none !important;

  input:active, button:active, .button:active
    transform: scale(.9);

  label
    font-size: .8em;
    color: #666;
    display: block;
    margin-bottom: .2em;

  .form-note
    color: #666;
    font-size: .9em;
    margin-top: .5em;

  .form-error
    font-size: .8em;
    color: $blue1;
    margin-top: .5em;

  input[disabled]
    background: $grey2;

  table
    border-collapse: collapse;

  th
    background: $grey2;

  td, th
    border: 1px solid #ddd;
    padding: .5em;

  .center
    text-align: center;

  button.small, .button.small
    display: inline-block;
    width: auto;
    padding: .3em 1em;
    background: $grey2;
    font-weight: 400;

  button.yellow, .yellow
    background: $yellow;
    color: #000;

  button.red, .red
    background: $red;
    color: $white;
    font-weight: 700;

  button.grey, .grey
    background: $grey2;
    color: #000;

  button.white, .white
    background: $white;
    color: #000;

  button.deny, .deny
    background: $white;
    color: #555;

  .button-small-red
    display: inline-block;
    width: auto;
    padding: .3em 1em;
    background: $red;
    font-weight: 400;

  .show-enter-active, .show-leave-active
    transition: all .5s ease;
    overflow: hidden;

  .show-leave, .show-enter-to
    max-height: 100vh;
    opacity: 1;

  .show-enter, .show-leave-to
    max-height: 0;
    opacity: 0;

  .show-del1-enter-active, .show-del1-leave-active
    overflow: hidden;

  .show-del1-leave, .show-del1-enter-to
    animation: show-del1-enter .5s .5s linear

  .show-del1-enter, .show-del1-leave-to
    animation: show-del1-leave .5s .5s linear

  @keyframes show-del1-enter
    100%
      max-height: 100vh;
      opacity: 1;

  @keyframes show-del1-leave
    100%
      max-height: 0;
      opacity: 0;

  .pull-down-container
    overflow: auto;

  .pull-down-header
    background-color: #eee !important;
    max-height: 60px;

  .refresh .pull-down-header
    height: 60px !important;

  .refresh .pull-down-content--icon
    animation: rotate 1s linear infinite

  @keyframes rotate
    0%
      transform: rotate(180deg)
    100%
      transform: rotate(540deg)

  .pull-down-content
    color: #666;

  .show-del2-enter-active
    transform: translateY(50px)
    animation: show-del2-enter .5s .5s ease forwards

  .show-del2-leave-active
    animation: show-del2-leave .5s ease

  .show-del2-leave-active #Send-textarea
    height: 1.8em

  @keyframes show-del2-enter
    100%
      transform: translateY(0)

  @keyframes show-del2-leave
    100%
      transform: translateY(50px)

</style>



