<template>
  <div class="Send" id="Send">
    <form class="form" @submit.prevent="send">
      <textarea
        type="text" id="Send-textarea" class="Send-textarea" resizable="false"
        v-model="sendMsg"
        :rows="rows" :placeholder="placeholder" :disabled="disabled || !token || !user0connect || !user1connect"
        @keydown="lines">
      </textarea><button type="submit" class="Send-submit">‚Üë</button>
    </form>
  </div>
</template>

<script>
import { t } from 'localizify'
let shift = false
let shiftTimer = null

export default {
  name: 'Send',
  data () {
    return {
      t,
      sendMsg: '',
      rows: 1,
      disabled: false
    }
  },
  props: ['user0connect', 'user1connect'],
  computed: {
    token: function () {
      return !!this.$store.state.user.tokens
    },
    placeholder: function () {
      if (!this.token) return t('Sign in to send messages')
      if (!this.user0connect) return t('Connection error')
      if (!this.user1connect) return this.$store.state.game.user1.name + ' ' + t('offline')
      return t('Message')
    }
  },
  methods: {
    lines (event) {
      if (event.key === 'Shift') {
        shift = true
        clearTimeout(shiftTimer)
        shiftTimer = setTimeout(() => {
          shift = false
        }, 500)
      }
      if (!shift && (event.key === 'Enter')) {
        event.preventDefault()
        this.send(event)
        return
      }
      let sendTextarea = document.getElementById('Send-textarea')
      let width = sendTextarea.clientWidth / 10
      let lines = 0
      this.sendMsg.split('\n').map(function (s) {
        lines++
        if (s.length > width) {
          lines += ~~(s.length / width)
        }
      })
      this.rows = lines
    },
    send (event) {
      if (!this.sendMsg.trim() || event.shiftKey) {
        return
      }
      this.disabled = true
      this.$store.dispatch('sendMessage', this.sendMsg.trim())
        .then(() => {
          this.sendMsg = ''
          this.rows = 1
          this.disabled = false
        })
    }
  }
}
</script>

<style lang="sass">
  .Send
    position: absolute;
    width: 100vw;
    bottom: 0;
    background: $grey1;
    padding: .5em 1em;
    box-sizing: border-box;
    border-top: 1px solid #fff;
    transition: all .3s ease;
    z-index: 3;

  .Send .form
    max-width: 500px;

  .Send-textarea
    width: calc(100% - 2.2em);
    display: inline-block;
    vertical-align: bottom;
    padding: .3em .6em;
    border-radius: 1em;
    resize: none;
    overflow: hidden;
    transition: all .3s ease;

  .Send-submit
    display: inline-block;
    position: relative;
    width: 1.8em;
    height: 1.8em;
    border-radius: 4em;
    padding: .3em 0;
    vertical-align: bottom;
    margin-left: .4em;

  .slide-left-enter-active .Send, .slide-right-enter-active .Send, .slide-left-leave-active .Send, .slide-right-leave-active .Send
    transform: translateY(20px);
    opacity: 0;

</style>



