<template>
  <div>
    <nav class="nav-boarduser">
      <transition-group>
        <div class="nav-boarduser__butto p0" key="0" :class="{ active: currentBoard(0), disposition: disposition0, lost: !user0connect }">
          <div class="nav-boarduser__button" @click="switchBoard(0)">{{ playername0 }}</div>
          <div class="nav-boarduser__life" :class="lifeClass(0)">
            <transition name="show">
              <Life v-if="!disposition0" :player="0" />
            </transition>
          </div>
        </div><div class="nav-boarduser__vs" :class="turnClass" key="2">vs
        </div><div class="nav-boarduser__butto p1" key="1" :class="{ active: currentBoard(1), disposition: disposition1, lost: !user1connect }">
          <div class="nav-boarduser__button" @click="switchBoard(1)">{{ playername1 }}</div>
          <div class="nav-boarduser__life" :class="lifeClass(1)">
            <transition name="show">
              <Life v-if="!disposition1" :player="1" />
            </transition>
          </div>
        </div>
      </transition-group>
    </nav>
    <texts></texts>
    <transition name="show">
      <boats v-if="disposition0"></boats>
    </transition>
    <transition name="show">
      <div v-if="endgame" class="new-game">
        <button class="small red" @click="newGame">New game?</button>
      </div>
    </transition>
    <flickity id="Boards" ref="flickity" class="boarduser-boards" :options="flickityOptions" :style="boardWidth" :class="{ disposition: disposition0 }">
      <board  v-for="(player, i) in players" :key="i" :player="i"></board>
    </flickity>
    <messages/>
  </div>
</template>
<script>
import Texts from '@/components/Texts'
import Messages from '@/components/Messages'
import Boats from '@/components/Boats'
import Board from '@/components/Board'
import Life from '@/components/Life'
import Flickity from 'vue-flickity'

export default {
  name: 'Basic',
  data () {
    return {
      players: false,
      playername0: false,
      playername1: false,
      flickityOptions: {
        prevNextButtons: false,
        pageDots: false,
        dragThreshold: 10,
        draggable: false
      },
      linksecond: false,
      endgame: false,
      disposition0: false,
      disposition1: false,
      ready: false
    }
  },
  props: ['user0connect', 'user1connect'],
  computed: {
    boardWidth: function () {
      return 'width:' + this.$store.state.gameDisplay.boardWidth + 'px; height:' + this.$store.state.gameDisplay.boardWidth + 'px;'
    },
    land: function () {
      return this.$store.state.gameDisplay.land
    },
    version: function () {
      return this.$store.state.game.__v
    },
    turnClass: function () {
      let ret = ''
      if (this.ready) {
        if (this.turn(0)) {
          ret = 'p0'
        } else if (this.turn(1)) {
          ret = 'p1'
        }
      }
      return ret
    }
  },
  methods: {
    newGame: function () {
      this.switchBoard(0)
      this.$store.dispatch('newGame', this.endgame)
    },
    lifeClass: function (n) {
      let ret = ''
      let life = this.$store.state.game.player(n).boat.c
      if (life < 40) {
        ret = 'critical'
      }
      return ret
    },
    turn: function (n) {
      return (this.$store.state.game.turn === this.$store.state.game['playerId' + n])
    },
    currentBoard: function (i) {
      return (this.$store.state.currentBoard === i)
    },
    switchBoard (n) {
      if (this.disposition0) {
        if (n) {
          this.$router.push('/game')
        } else {
          this.$router.push('/user')
        }
      } else {
        this.$refs.flickity.select(n)
      }
    },
    selectBoard () {
      let that = this
      this.$refs.flickity.on('select', function () {
        that.$store.commit('setBoard', this.selectedIndex)
      })
    },
    watchPhase () {
      this.disposition0 = (this.$store.state.game.player().phase === 'disposition')
      this.disposition1 = (this.$store.state.game.player(1).phase === 'disposition')
      this.ready = this.$store.state.game.phase('ready')
      if (this.land !== 2) { // on land2 flc is off
        if (!this.disposition0) { // no draggable on disposition
          let flc = typeof this.$refs.flickity
          if (flc !== 'undefined') {
            this.$refs.flickity.destroy()
            this.flickityOptions.draggable = true
            this.$refs.flickity.init()
          }
          this.selectBoard()
        }
      }
      this.linksecond = !this.$store.state.game.playerId1
    },
    watchLand (n) {
      let flc = typeof this.$refs.flickity
      if ((n === 2) && (flc !== 'undefined')) {
        this.$refs.flickity.destroy()
      } else if ((n !== 2) && (flc === 'undefined')) {
        this.watchPhase()
      }
    },
    watchVersion () {
      this.disposition0 = (this.$store.state.game.player().phase === 'disposition')
      this.disposition1 = (this.$store.state.game.player(1).phase === 'disposition')
      this.ready = this.$store.state.game.phase('ready')
      this.playername0 = this.$store.state.game.user0.name
      this.playername1 = this.$store.state.game.user1.name
      this.players = this.$store.state.game.playersIDs
      let currentBoard = this.$store.state.currentBoard
      let newBoard = 1 - this.$store.state.currentBoard
      if (this.ready && this.turn(currentBoard)) {
        setTimeout(() => {
          this.switchBoard(newBoard)
        }, 500)
      }
      this.linksecond = !this.$store.state.game.playerId1
      this.endgame = this.$store.state.game.newgame
    }
  },
  mounted () {
    this.$nextTick(function () {
      this.watchPhase()
    })
  },
  created () {
    this.watchVersion()
  },
  watch: {
    land (n) {
      this.watchLand(n)
    },
    disposition0 () {
      this.watchPhase()
    },
    version () {
      this.watchVersion()
    }
  },
  components: {
    Texts,
    Messages,
    Boats,
    Board,
    Flickity,
    Life
  }
}
</script>
<style lang="sass">
  .nav-boarduser
    text-align: center;
    padding: 1rem 1rem 0;
    box-sizing: border-box;
    margin: 0 auto;
    max-width: 1020px;

  .nav-boarduser__butto
    display: inline-block;
    color: #999;
    white-space: nowrap;
    user-select: none;
    position: relative;
    width: calc(50% - 1.2rem);
    text-align: left;
    opacity: .3;
    transform-origin: 50% 50%;
    transition: all .2s ease;
    padding-bottom: 5px;

  .nav-boarduser__butto.p0
    text-align: right;

  .lost .nav-boarduser__button:before
    content: '‚õî';
    font-size: .8em;
    margin-right: .3em;
    line-height: 1em;

  .nav-boarduser__button
    overflow: hidden;
    text-overflow: ellipsis;
    color: #000;
    cursor: pointer;
    white-space: nowrap;
    user-select: none;

  .nav-boarduser__life
    position: absolute;
    height: 3px;
    width: 100%;
    bottom: 0;
    text-align: left;

  .disposition .nav-boarduser__life
    background: #ccc;

  .p0 .nav-boarduser__life
    transform: scale(-1, 1);

  .nav-boarduser__life.critical .Life-boat .Life-boat__i
    background: $red;

  .nav-boarduser__butto:active
    transform: scale(.8);

  .nav-boarduser__butto.active
    opacity: 1;

  .nav-boarduser__vs
    display: inline-block;
    width: 2.4rem;
    color: #999;
    font-size: .5em;
    font-style: italic;
    vertical-align: top;
    line-height: 1.4rem;
    position: relative;

  .nav-boarduser__vs.p0, .nav-boarduser__vs.p1
    color: #fff;

  .nav-boarduser__vs.p0:before, .nav-boarduser__vs.p1:after
    content: '';
    position: absolute;
    width: 100%;
    height: .8rem;
    background: $img-cannon no-repeat 30% 50%;
    background-size: contain;
    opacity: 1;
    top: .2rem;
    left: 50%;
    transform: translateX(-50%);
    transition: all 1s ease;

  .nav-boarduser__vs.p1:after
    transform: translateX(-50%) scale(-1, 1);
    filter: grayscale(1);
    opacity: .5;

  .boarduser-boards
    position: relative;
    margin: 0 auto;
    user-select: none;
    outline: none !important;
    text-align: center;
    white-space: nowrap;

  .flickity-viewport
    min-height: 100%;
    overflow: hidden;

  .land2 .nav-boarduser
    display: block;

  .land2 .boarduser-boards
    width: 100% !important;

  .land2 .Board+.Board
    margin-left: 20px;

  .land2 .boarduser-boards.disposition .Board.player1
    display: none;

  .new-game
    text-align: center;
    margin-top: .7em;

  .link-second
    text-align: center;
    margin-top: 1em;

</style>



