import shortid from 'shortid'

export default class User {
  constructor (own = false) {
    if (!own) {
      this.name = 'Anon'
      this._id = 0
      this.connect = false
      return
    }
    this.name = User._generateName()
    this._id = shortid.generate()
    this.phase = 'new'
    this.lang = 'en'
    this.tokens = null
    this.app = process.env.APP_VERSION
    this.__v = 0
  }

  static _generateName () {
    let captainId = Math.floor(Math.random() * User.prototype.captainNames.length)
    let captainName = User.prototype.captainNames[captainId]
    User.prototype.captainNames.splice(captainId, 1)
    return captainName
  }

  static isValid (test) {
    if (!test || !test._id || !test.name || !test.phase || !test.app) {
      return false
    }

    let v0 = test.app.split('.')
    let v1 = process.env.LS_VERSION.split('.')
    if (v0.length !== v1.length) return false
    for (let i = 0; i < v0.length; i++) {
      if (v0[i] < v1[i]) return false
    }

    return true
  }
}

User.prototype.captainNames = [
  'Hector Barbossa',
  'Jack Sparrow',
  'Davy Jones',
  'Calipso',
  'Bootscrap Bill',
  'Blackbeard',
  'Alexander Smollett',
  'Ben Gunn',
  'Billy Bones',
  'Jim Hawkins',
  'John Silver',
  'John Trelawney',
  'David Livesey',
  'Captain Flint'
]



